'use client';
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Heading } from "@/components/ui/heading";
import { Input } from "@/components/ui/input";
import { useState } from 'react';
import { useRouter } from 'next/navigation';
import Link from "next/link";
import axios from 'axios'
import api from '@/lib/api' // 使用我们创建的实例


export default function Login() {
  // 状态管理：记录当前激活的标签页
  const [activeTab, setActiveTab] = useState('login');
  const [formData, setFormData] = useState({
    phone_number: '',
    password: ''
  })
  const [error, setError] = useState('')
  const [isLoading, setIsLoading] = useState(false)

// 输入处理 ================================
  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { id, value } = e.target
    setFormData(prev => ({
      ...prev,
      [id]: value
    }))
    if(error) setError('')
  }

  // 提交处理 ================================
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    // 前端验证
    if (!/^\d{11}$/.test(formData.phone_number)) {
      setError('手机号必须为11位数字')
      return
    }

    try {
      setIsLoading(true)
      
      // 调用登录接口
      const response = await api.post('/auth/login', {
        phone_number: formData.phone_number,
        password: formData.password
      })
      const router = useRouter();

      // 存储令牌
      localStorage.setItem('token', response.data.access_token)
      
      // 跳转到主页面
      router.push('/main'); // 确保这行代码存在
    } catch (err) {
      if (axios.isAxiosError(err)) {
        setError(err.response?.data.detail || '登录失败')
      } else {
        setError('网络连接异常')
      }
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-white">
      {/* 顶部标签页容器（添加背景色） */}
      <div className="w-98 max-w-md mb-6 bg-gray-50 rounded-lg p-1">
        <div className="flex">
          {/* 登录按钮（根据状态动态设置样式） */}
          <Button
            variant={activeTab === 'login' ? 'secondary' : 'outline'}
            className={`w-48 py-2 transition-all duration-200 ${
              activeTab === 'login' ? 'bg-primary text-white' : 'text-gray-600 hover:bg-gray-200'
            }`}
            onClick={() => setActiveTab('login')}
          >
            登录账号
          </Button>
          
          {/* 注册按钮（根据状态动态设置样式） */}
          <Link 
            href="/register" >
          <Button
            variant={activeTab === 'register' ? 'secondary' : 'outline'}
            className={`w-48 py-2 transition-all duration-200 ${
              activeTab === 'register' ? 'bg-primary text-white' : 'text-gray-600 hover:bg-gray-200'
            }`}
            onClick={() => setActiveTab('register')}
          >
            注册账号
          </Button>
          </Link>
        </div>
      </div>
      {/* 登录表单 */}
      <Card className="flex flex-col items-start space-y-0 p-5 w-96">
        <Heading as="h2" size="lg">
          登录账号
        </Heading>
        <p className="text-gray-500 -mt-4">
          欢迎回来
        </p>
        <div className="flex flex-col space-y-4 w-full">
          <label htmlFor="phone_number" className="text-sm">
            手机号
          </label>
          <Input
            id="phone_number"
            placeholder="请输入手机号"
            className="w-full"
            value={formData.phone_number}
            onChange={handleInputChange}
            disabled={isLoading}
          />
          <label htmlFor="password" className="text-sm">
            密码
          </label>
          <Input
            id="password"
            placeholder="请输入密码"
            type="password"
            className="w-full"
            value={formData.password}
            onChange={handleInputChange}
            disabled={isLoading}
          />

          {/* 错误提示 */}
          {error && (
            <div className="text-red-500 text-sm">
              ⚠️ {error}
            </div>
          )}

          <Button 
            className="px-8 py-2 w-full mt-5"
            onClick={handleSubmit} // 绑定提交函数
            disabled={isLoading} // 加载时禁用
          >
            {isLoading ? '登录中...' : '登录'}
          </Button>
        </div>
      </Card>
    </div>
  );
}